import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router';
import HomeView from '../views/HomeView.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    children: [
      {
        path: '/',
        name: 'home',
        component: () => import('../views/core/HomeView.vue'),
      },
      {
        path: 'bulletin',
        name: 'bulletin',
        component: () => import('../views/core/BulletinView.vue'),
      },
      {
        path: 'account',
        name: 'account',
        component: () => import('../views/core/AccountView.vue'),
      },
      {
        path: 'settings',
        name: 'settings',
        component: () => import('../views/core/SettingsView.vue'),
      },
    ],
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/LoginView.vue'),
  },
  {
    path: '/athletics/:gameCode/:gameId',
    name: 'athletics',
    component: () => import('../views/AthleticsView.vue'),
    children: [
      {
        path: '/athletics/:gameCode/:gameId',
        name: 'athleticsMain',
        component: () => import('../views/athletics/MainView.vue'),
      },
      {
        path: 'registration',
        name: 'athleticsRegistration',
        component: () => import('../views/general/RegistrationView.vue'),
      },
    ],
  },
  {
    path: '/general/:gameCode/:gameId',
    name: 'general',
    component: () => import('../views/GeneralView.vue'),
    children: [
      {
        path: '/general/:gameCode/:gameId',
        name: 'generalMain',
        component: () => import('../views/general/MainView.vue'),
      },
      {
        path: 'registration',
        name: 'generalRegistration',
        component: () => import('../views/general/RegistrationView.vue'),
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

// eslint-disable-next-line
router.beforeEach((to, from) => {
  const isLogin = localStorage.sep5_admin_token;
  if (isLogin) {
    const temp = JSON.parse(localStorage.sep5_admin_data);
    const expire = new Date(temp.expired).getTime();
    const current = Date.now();
    if (expire <= current && to.path !== '/login') {
      localStorage.removeItem('sep5_admin_token');
      localStorage.removeItem('sep5_admin_data');
      // eslint-disable-next-line
      alert('請重新登入');
      return '/login';
    }
  } else {
    // eslint-disable-next-line
    if (to.path !== '/login') {
      // eslint-disable-next-line
      alert('請先登入');
      return '/login';
    }
  }
});

export default router;
