
import {
  defineComponent, reactive, ref, watch,
} from 'vue';
import { QuickFetch, QuickData } from '@/quick';
import { useRouter } from 'vue-router';
import { OnClickOutside } from '@vueuse/components';
import SmallModal from '@/components/SmallModal.vue';

export default defineComponent({
  setup() {
    const qf = new QuickFetch();
    const qd = new QuickData();
    const router = useRouter();
    const displayModal = ref(0);
    const data = reactive({
      old_password: '',
      password: '',
      password_confirm: '',
    });
    const errorList = reactive({
      password: {
        filled: false,
        format: true,
      },
      passwordConfirm: {
        filled: false,
        same: true,
      },
    });
    watch(data, () => {
      errorList.password.filled = data.password.length > 0;
      errorList.password.format = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/.test(data.password);
      errorList.passwordConfirm.same = data.password === data.password_confirm;
      errorList.passwordConfirm.filled = data.password_confirm.length > 0;
    });
    const userData: any = ref({});
    function getUserData() {
      qf.Url('auth/admin/info').GetAll(userData);
    }
    getUserData();
    function logout() {
      qf.Url('auth/admin/logout').Post().then((res: any) => {
        if (res.message === 'done') {
          localStorage.removeItem('sep5_admin_token');
          localStorage.removeItem('sep5_admin_data');
          router.push('/login');
        } else {
          qd.Alert('登出失敗');
        }
      });
    }
    function submitAll() {
      // eslint-disable-next-line no-restricted-syntax
      for (const item of Object.entries(errorList)) {
        // eslint-disable-next-line no-restricted-syntax
        for (const error of Object.entries(item[1])) {
          if (error[1] === false) {
            if (error[0] === 'filled') {
              qd.Alert('請確認所有欄位皆已填寫');
            } else {
              qd.Alert('請確認輸入的內容');
            }
            return;
          }
        }
      }
      const temp: any = JSON.parse(JSON.stringify(data));
      delete temp.password_confirm;
      qf.Url('auth/admin/change-password').Post(temp).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('變更成功');
          data.password = '';
          data.password_confirm = '';
          data.old_password = '';
        } else if (res.message === 'wrong_password') {
          qd.Alert('原始密碼輸入錯誤');
          data.password = '';
          data.password_confirm = '';
          data.old_password = '';
        }
      });
    }
    return {
      accountMenu: ref(false),
      logout,
      displayModal,
      userData,
      data,
      submitAll,
      errorList,
      openAccount: () => {
        displayModal.value = 1;
        data.password = '';
        data.password_confirm = '';
        data.old_password = '';
        getUserData();
      },
    };
  },
  components: {
    OnClickOutside,
    SmallModal,
  },
});
